@import "../../styles/sass/variables";
@import "../../styles/sass/mixins";


.button {
  @include text($textFontFamily, 14px, 400, 1.2);
  display: block;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 1px;
  padding: 7px 20px;
  transition: all 0.4s ease-in-out 0s;
  background: transparent;
  max-width: 170px;
  margin: 15px auto;
  z-index: 22;
  text-align: center;
  border-radius: 0px;
  border: none;
  position: relative;
  cursor: pointer;

  &::after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 10px;
    width: 60%;
    background: $primaryColor;
    z-index: -1;
    transition: all 0.2s ease-in-out 0s;
    margin: 0 auto;
  }
  &:hover{
    text-decoration: none;
    color: $textColor;
    &::after{
      width: 100%;
      height: 110%;
      transition: all 0.3s ease-in-out 0s;
    }
  }
}

