@import "../../common/styles/sass/mixins";
@import "../../common/styles/sass/variables";


.burgerNav {
 /* width: 100%;        /// my morning
  height: 100%;*/

  width: 100%;
  height: 220px;
  display: flex;
  justify-content: center;
  align-items: center;

  .burgerNavItems {

    @include text($titleFontFamily, 18px, 700, 1.2);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    background-color: #252527;
    ///* position: absolute; */
    /* right: 42%; */
    /* top: 0; */
    /* height: 130px; */
    /* padding: 20px; */
    transform: translate(0, -180px);
    transition: 0.7s;


    a {
      color: $textColor;
      text-decoration: none;
      margin: 3px;

      &:hover {
        text-decoration: none;
        color: $primaryColor;
        cursor: pointer;
      }
    }

    .active {
      color: $primaryColor;
    }
  }

  .show {
    transform: translate(0, 15px);
    //height: 130px;
  }

  @media screen and(min-width: 400px) {
    display: none;
  }

  .hamburger {
    position: fixed;
    right: 20px;
    top: 15px;
    width: 40px;
    height: 25px;
    border-top: 4px solid #fff;
    border-bottom: 4px solid #fff;
  }
  .hamburger:before {
    content: "";
    position: absolute;
    top: 10px;
    left: 0px;
    width: 100%;
    border-top: 5px solid #fff;
  }
}

